<style scoped>
  .onlyPlot{
    position: relative;
    height: 100%;
    width: 100%;
  }

  .centerPlot {
    position: absolute;
    left: 50%;
    top: 50%;
    transform:translate(-50%, -50%);
  }

  .onlyPlot-enter-active {
    animation: bounce-in 1.0s;
    transition: opacity 1.0s;
  }

  .onlyPlot-leave-active {
    transition: opacity 1.0s;
  }
  .onlyPlot-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }

  @keyframes bounce-in {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.2);
    }
    100% {
      transform: scale(1);
    }
  }

  .Plot {
    position: absolute;
    width: 90%;
    bottom: 5%;
    transform:translate(-50%, 0);
    left: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 12px;
    border:5px solid #ffc674;
  }

  .Plot p {
    margin-left: 30px;
    margin-right: 30px;
    font-size: 25px;
    font-weight: 500;
    color: white;
  }
</style>



